import React from "react";
import TrainLineItem from "../TrainLineItem/TrainLineItem";

import "./TrainLineList.sass";

function TrainLineList({ trainlines }) {
  const actualTrainLines = trainlines || [{}, {}, {}];

  return (
    <div className="TrainLineList">
      {/* <span className="NextTrain">Next Train</span> */}
      {actualTrainLines.map(tl => {
        return <TrainLineItem {...tl} />;
      })}
    </div>
  );
}

export default TrainLineList;
