import React from "react";
import IconTextPair from "../IconTextPair/IconTextPair";

import durationicon from "../../assets/tripduration.svg";
import locationicon from "../../assets/closeststation.svg";
import arrivalicon from "../../assets/arrivaltime.svg";
import "./Header.sass";

function Header({ closeststation, tripduration, arrivaltime }) {
  const actualClosestStation = closeststation || "-- --";
  const actualTripDuration = tripduration || "-- --";
  const actualArrivalTime = arrivaltime || "-- --";
  return (
    <div className="Header">
      <IconTextPair icon={locationicon}>{actualClosestStation}</IconTextPair>
      <IconTextPair icon={durationicon}>{actualTripDuration} min</IconTextPair>
      <IconTextPair icon={arrivalicon}>{actualArrivalTime}</IconTextPair>
    </div>
  );
}

export default Header;
