import React from "react";

import "./LineIndicators.sass";

function LineIndicators({ trainlines }) {
  const actualTrainLines = trainlines || [
    { line: "ROUTE 0" },
    { line: "ROUTE 0" },
    { line: "ROUTE 0" }
  ];

  const getlines = actualTrainLines.map(lines => lines.line);

  const uniqueLines = [...new Set(getlines)];

  return (
    <div className="LineIndicators">
      {uniqueLines.map(uln => {
        return (
          <div
            className={`LineIndicator LineIndicator--${uln.split(" ")[1]}`}
          />
        );
      })}
    </div>
  );
}

export default LineIndicators;
