import React, { useState, useEffect } from "react";
import ky from "ky";

import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import TrainLineList from "./components/TrainLineList/TrainLineList";
import LineIndicators from "./components/LineIndicators/LineIndicators";

import "./App.sass";
import useGeolocation from "./util/useGeolocation";
import useInterval from "./util/useInterval";

import bartdata from "./assets/bart.json";

function distance(place1, place2) {
  function toRad(x) {
    return (x * Math.PI) / 180;
  }
  var lat1 = place1[0];
  var lon1 = place1[1];
  var lat2 = place2[0];
  var lon2 = place2[0];

  var R = 6371;
  var x1 = lat2 - lat1;
  var dLat = toRad(x1);
  var x2 = lon2 - lon1;
  var dLon = toRad(x2);
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRad(lat1)) *
      Math.cos(toRad(lat2)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var d = R * c;
  return d;
}

var parenRegex = /(?<=\().+?(?=\))/;

function App() {
  const [tripduration, settripduration] = useState(null);
  const [arrivaltime, setarrivaltime] = useState(null);

  const [trainlines, settrainlines] = useState(null);
  const [lineindicators, setlineindicators] = useState(null);

  const { latitude, longitude } = useGeolocation();
  const [userlocation, setuserlocation] = useState(null);
  const [originstation, setoriginstation] = useState(null);

  const cmd = "DEPART";
  const dest = "19th";
  const a = "3";

  const getAndSetTrainLines = async () => {
    if (originstation) {
      const bartRes = await getTrainLines();
      const { trainlines, arrivaltime, tripduration } = await getTrainLines();
      settrainlines(trainlines);
      setarrivaltime(arrivaltime);
      settripduration(tripduration);
    }
  };

  async function getTrainLines() {
    const parsed = await ky.get(
      `http://api.bart.gov/api/sched.aspx?cmd=${cmd}&orig=${originstation}&dest=${dest}&date=now&key=MW9S-E7SL-26DU-VV8V&b=0&a=${a}&json=y`
    );

    const res = await parsed.json();
    console.log(res);

    const trips = res.root.schedule.request.trip;

    const tripduration = trips[0]["@tripTime"];
    const arrivaltime = trips[0]["@destTimeMin"];
    const trainlines = trips.map(trip => ({
      origintime: trip.leg[0]["@origTimeMin"],
      line: trip.leg[0]["@line"]
    }));

    return {
      trainlines,
      arrivaltime,
      tripduration
    };
  }

  useEffect(() => {
    setuserlocation([latitude, longitude]);
    const stationdistances = bartdata.features.map(feat => {
      return distance(
        [latitude, longitude],
        [feat.geometry.coordinates[0], feat.geometry.coordinates[1]]
      );
    });
    setoriginstation(
      parenRegex.exec(
        bartdata.features[
          stationdistances.reverse().indexOf(Math.min(...stationdistances))
        ].properties.Name
      )[0]
    );
  }, [latitude, longitude]);

  useEffect(() => {
    getAndSetTrainLines();
  }, [originstation]);

  useInterval(getAndSetTrainLines, 60 * 1000);

  return (
    <>
      <Header
        closeststation={originstation}
        tripduration={tripduration}
        arrivaltime={arrivaltime}
      />
      {/* {latitude}, {longitude} */}
      <LineIndicators trainlines={trainlines} />
      <TrainLineList trainlines={trainlines} />
      <Footer />
    </>
  );
}

export default App;
