import React from "react";
import dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";

import "./TrainLineItem.sass";

dayjs.extend(relativeTime);

const regex = /(\d{1,2}):(\d{2}) (AM|PM)/;
function TrainLineItem({ line, origintime }) {
  if (!origintime) {
    return <div className="TrainLineItem">â€”</div>;
  }
  const extractedTime = regex.exec(origintime);

  const dayTime = dayjs()
    .hour(parseInt(extractedTime[1]) + (extractedTime[3] === "PM" ? 12 : 0))
    .minute(extractedTime[2]);

  return (
    <div className="TrainLineItem">
      {/* {dayjs().diff(dayTime, "minute")} */}
      {dayjs(dayTime).diff(dayjs(), "minute")} min
      {/* {dayTime.fromNow()} */}
    </div>
  );
}

export default TrainLineItem;
